.add-new__input-wrapper {
  position: relative;
  width: 48%;
  margin: 25px 0 0;
  min-height: 255px;
  box-sizing: border-box;
}

@media screen and (max-width: 550px) {
  .add-new__input-wrapper {
    font-size: 16px;
    width: 100%;
    min-height: 120px;
    margin: 25px 0 0;
  }
}