.sign-in-up-req__btn_publish {
  width: 190px;
  font-size: 19px;
  margin: 40px 0 60px;
  transition: all .5s ease;
}

.sign-in-up-req__btn_publish:hover {
  background: #632EBA;
}

@media screen and (max-width: 1024px) {
  .sign-in-up-req__btn_publish {
    width: 180px;
    font-size: 16px;
    margin-top: 20px;
  }
}

@media screen and (max-width: 700px) {
  .sign-in-up-req__btn_publish {
    justify-self: center;
  }
}